const uploadZone = document.querySelector(".upload-zone")
const dropFileZone = document.querySelector(".upload-zone_dragover")
const statusList = document.getElementById("uploadForm_Status-list")
// const statusText = document.getElementById("uploadForm_Status")
const uploadInput = document.querySelector(".form-upload__input")

let file;

let setStatus = (text) => {
  let statusText = document.createElement('p');
  statusText.textContent = text;
  statusList.append(statusText)
}

["dragover", "drop"].forEach(function(event) {
  document.addEventListener(event, function(evt) {
    evt.preventDefault()
    return false
  })
})

dropFileZone.addEventListener("dragenter", function() {
  uploadZone.classList.add("_active")
  // setStatus("")
})

dropFileZone.addEventListener("dragleave", function() {
  uploadZone.classList.remove("_active")
})

let filesArray = []

dropFileZone.addEventListener("drop", function(e) {
  uploadZone.classList.remove("_active")
  const file = e.dataTransfer?.files[0]
  const files = e.dataTransfer?.files
  for (var i = 0; i < files.length; i++) {
    filesArray.push(files[i]);
}
// debugger
  // filesArray.push('1')
  setStatus('Вы прикрепили файл: ' + file.name)
  // console.log(files)
  // console.log(file)
  // console.log(e)
  // console.log(filesArray)
  // console.log(e.dataTransfer?.files)
  // console.log(e.dataTransfer)
  if (!file) {
    return
  }

  if (file.type.startsWith("image/")) {
    uploadInput.files = e.dataTransfer.files
    console.dir(uploadInput.files)
    // processingUploadFile()
  } else {
    setStatus("Можно загружать только изображения")
    return false
  }
})


var img = document.createElement("img");
    img.classList.add("obj");
    img.file = file;
    uploadZone.appendChild(img); // Предполагается, что "preview" это div, в котором будет отображаться содержимое.
