const buttons = document.querySelectorAll('.nav__item:not(.js-item)')
// console.log(buttons);
// const center = document.querySelector('.center')
const headerTitle = document.querySelector('.header__title')
let sections = document.querySelectorAll('[data-targetText]')
let targetText;
for (let i = 0; i < buttons.length; i++) {
  buttons[i].addEventListener('click', function () {
    targetText = buttons[i].querySelector('.nav__link').textContent
    // console.log(targetText);

    sections.forEach(section => {
      section.classList.remove('visible')
    })
    sections.forEach(section => {
      if(section.dataset.targettext == targetText)
      section.classList.add('visible')
    })
    headerTitle.textContent = targetText
    headerTop.removeAttribute("data-profile")
  })
}

// --------------------------------------------------------------------------

const headerTop = document.querySelector('.header__top')
const buttonsProfile = document.querySelectorAll('.profile__img-wrap')
const center = document.querySelector('.center')

// console.log(headerTop);


for (let i = 0; i < buttonsProfile.length; i++) {
  buttonsProfile[i].addEventListener('click', function () {
    let curentSectionVisible = document.querySelector('[data-targetText].visible')
    // console.log(curentSectionVisible);
    if(!(curentSectionVisible === document.querySelector('.center'))) {
      curentSectionVisible.dataset.current = true;
    }
    // console.log(curentSectionVisible);
    center.classList.toggle('visible')

    if(center.classList.contains('visible')) {
      sections.forEach(section => {
        section.classList.remove('visible')
      })
      center.classList.add('visible')
      headerTitle.textContent = 'Профиль'
      headerTop.setAttribute("data-profile", "")
    }
    else {
      sections.forEach(section => {
        section.classList.remove('visible')
        headerTop.removeAttribute("data-profile")
      })
      document.querySelector('[data-current="true"]').classList.add('visible')
      headerTitle.textContent = document.querySelector('[data-current="true"]').dataset.targettext
      delete document.querySelector('[data-current="true"]').dataset.current

    }
  })
}
