import Choices from "choices.js"
// console.dir(Choices);

const choices = document.querySelectorAll('[data-select]')

choices.forEach(el => {
  new Choices(el, {
    allowHTML: true,
    itemSelectText: '',
    placeholder: true,
    placeholderValue: 'Label',
    searchEnabled: false,
    classNames: {
      containerOuter: 'choices input',
    }
  });
})

const choicesBg = document.querySelectorAll('[data-select-bg]')
// const choicesBg = document.querySelector('[data-select-bg]')

let ch;

choicesBg.forEach(el => {
  ch = new Choices(el, {
    allowHTML: true,
    itemSelectText: '',
    placeholder: true,
    placeholderValue: 'Label',
    searchEnabled: false,
    classNames: {
      containerOuter: 'choices input --bg',
    },
    callbackOnInit: functionInit,
  });
  ch.passedElement.element.addEventListener(
    'change',
    functionInit,
    false,
  );
})

function functionInit() {
  let data = document.querySelectorAll('[data-value*="url"]')
  // console.log(data);
  data.forEach(ch => {
    let url = ch.dataset.value
    ch.style.backgroundImage = url
  })
}

