const list = document.querySelector('.center .center__form-list')
const tabs = document.querySelectorAll('.center .center__form-link')
const tabContent = document.querySelectorAll('.center [data-tab-content]')
// console.log(tabContent)


list.addEventListener('click', (e) => {
  // console.log(e.target.dataset.tab)
  // console.log(e.target)
  let currentTabValue = e.target.dataset.tab
  tabs.forEach(el => {
    el.classList.remove('--active')
  })
  e.target.classList.add('--active')

  tabContent.forEach(el => {
    // debugger
  // console.log(el.dataset.tabContent)

    if(el.dataset.tabContent == currentTabValue) {

      el.classList.add('--active')
    }
    else {
      el.classList.remove('--active')
    }
  })
})
