let dataThemeButtons = document.querySelectorAll('[data-theme-button]')
// console.log(dataThemeButton.getAttribute('data-theme-button'))
// console.log(dataThemeButtons)

dataThemeButtons.forEach(el => {
  el.addEventListener('click', () => {
    if (document.documentElement.hasAttribute('data-theme-dark')) {
      document.documentElement.removeAttribute('data-theme-dark')
    }
    else {
      document.documentElement.setAttribute('data-theme-dark', '')
    }
  })
})

