const authenticWide = document.querySelector('.authentic.--wide')

const ro = new ResizeObserver(entries => {
  // debugger
  for(let entry of entries){

    const width = entry.borderBoxSize[0].inlineSize
    // console.log(entries);
    // console.log(width);

      if(width > 449) {
        authenticWide.classList.add('min-width-450px')
      }
      else {
        authenticWide.classList.remove('min-width-450px')
      }
      if(width > 599) {
        authenticWide.classList.add('min-width-600px')
      }
      else {
        authenticWide.classList.remove('min-width-600px')
      }
  }
})

ro.observe(authenticWide)
