import Inputmask from "inputmask";
// console.log(Inputmask)

let inputTel = document.querySelector(".center__form-input-tel");
// console.log(inputTel)

var im = new Inputmask("+7(999)-999-99-99", {
  "placeholder": "0",
  clearMaskOnLostFocus: false
});
im.mask(inputTel);

