const siteContainer = document.querySelector('.site-container')
const mainContentWidth = document.querySelector('main')

const ro = new ResizeObserver(entries => {
  // debugger
  for(let entry of entries){

    const width = entry.contentBoxSize[0].inlineSize
    // console.log(entries);
    // console.log(width);

      if(width > 749) {
        siteContainer.classList.add('main-min-width-750px')
      }
      else {
        siteContainer.classList.remove('main-min-width-750px')
      }
      // if(width > 599) {
      //   siteContainer.classList.add('min-width-600px')
      // }
      // else {
      //   siteContainer.classList.remove('min-width-600px')
      // }
  }
})

ro.observe(mainContentWidth)
