const tableBodies = document.querySelectorAll('.invest__table-body')
// const rows = document.querySelectorAll('.invest__table-row')
// const inners = document.querySelectorAll('.invest__table-row .inner')

// console.log(rows)

const ro = new ResizeObserver(entries => {
  // debugger
  // console.log(entries);
  for(let entry of entries){

    const invest = entry.target.closest('.invest')
    const table = entry.target.closest('.invest__table')
    const rows = table.querySelectorAll('.invest__table-row')
    const inners = table.querySelectorAll('.invest__table-row .inner')


    const width = entry.borderBoxSize[0].inlineSize
    // console.log(entries);
    // console.log(entry.target);
    // console.dir(entry.target);
    // console.log(width);

      if(width > 600){
        inners.forEach(inner => {
          inner.querySelectorAll('.td').forEach(td => {
            inner.parentNode.append(td)
            inner.classList.add('hidden')
            // invest.classList.add('inners-hidden')
            table.classList.add('inners-hidden')
            invest.classList.add('inners-hidden')
          })
        })
      }
      else {
        rows.forEach(row => {
          // debugger
          // console.log(row.querySelectorAll('.invest__table-row > .td'))
          if (row.querySelectorAll('.invest__table-row > .td').length !== 0) {
            row.querySelectorAll('.td:nth-child(n+3):nth-child(-n+6)').forEach(td => {
              row.querySelector('.inner').append(td)
              row.querySelector('.inner').classList.remove('hidden')
              // invest.classList.remove('inners-hidden')
              table.classList.remove('inners-hidden')
              invest.classList.remove('inners-hidden')
            })
            row.querySelectorAll('.invest__table-row > .td').forEach(td => {
              row.querySelector('.inner:nth-child(2)').append(td)
              row.querySelector('.inner:nth-child(2)').classList.remove('hidden')
            })
          }
        })
      }

      // if(width > 449) {
      //   authenticWide.classList.add('min-width-450px')
      // }
      // else {
      //   authenticWide.classList.remove('min-width-450px')
      // }
  }
})

tableBodies.forEach(tableBody => {
  ro.observe(tableBody)
})



