let buttonsLeft = document.querySelectorAll('.invest__pagination-left .invest__pagination-button')

document.addEventListener('click', (e) => {
  if (e.target.closest('.invest__pagination-left .invest__pagination-button')) {
    buttonsLeft.forEach(buttonLeft => {
      buttonLeft.classList.remove('invest__pagination-button--active')
    })
    e.target.closest('.invest__pagination-left .invest__pagination-button').classList.add('invest__pagination-button--active')
  }
  else {
    // console.log(e.target.closest('.nav__item'))
  }
})


